<template>
    <div class="container">
        <h3 class="text-center">UF</h3>
        <div class="container">
            <form @submit="save">
                <div v-if="errors.length">
                    <div class="alert alert-warning" v-bind:key="index" v-for="(error, index) in errors">{{error}}</div>
                </div>
                <fieldset class="form-group">
                    <label>Id</label>
                    <input type="text" class="form-control" v-model="uf.id" disabled>
                </fieldset>
                <fieldset class="form-group">
                    <label>Sigla</label>
                    <input type="text" class="form-control" v-model="uf.sigla">
                </fieldset>
                <fieldset class="form-group">
                    <label>Nome</label>
                    <input type="text" class="form-control" v-model="uf.nome">
                </fieldset>
                <button class="btn btn-success" type="submit">Salvar</button>
            </form>
        </div>
    </div>
</template>
<script>
import UFDataService from "../service/UFDataService";
export default {
    name: "UF",
    data() {
        return {
            uf: {},
            errors: []
        };
    },
    computed: {
        id() {
            return this.$route.params.id;
        }
    },
    methods: {
        refresh() {
            if(this.id > 0){
                UFDataService.retrieve(this.id)
                    .then(response => {
                    this.uf = response.data
                });
            }
        },
        save(e) {
            e.preventDefault();
            if(this.id > 0){
                UFDataService.update(this.id, this.uf, {
                    id: this.id,
                    uf: this.uf
                }).then(() => {
                    this.$router.push('/ufs')
                }).catch(e => {
                    console.log(e)
                    if(e.response){
                        this.$store.dispatch('setSnackbar', {
                          showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                        });
                    }
                });
            } else {
                UFDataService.create(this.uf, {
                    uf: this.uf
                }).then(() => {
                    this.$router.push('/ufs')
                }).catch(e => {
                    console.log(e)
                    if(e.response){
                        this.$store.dispatch('setSnackbar', {
                          showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                        });
                    }
                });
            }
        }
    },
    created() {
        this.refresh()
    }
};
</script>